import React from "react";
import "hui/step-flow/flow-landing";

export default class HAFlowLanding extends React.Component {
    static propTypes = {
        className: React.PropTypes.string
    };

    constructor(props) {
        super(props);
    }

    render() {
        /* jshint ignore:start */
        return (
            <ha-flow-landing class={this.props.className} {...this.props}>
                {this.props.children}
            </ha-flow-landing>
        );
        /* jshint ignore:end */
    }
}
